import Component from "@egjs/component";
import Axes, {PanInput} from "@egjs/axes";
import SpriteImage from "./SpriteImage";
import {VERSION} from "../version";

const DEFAULT_PAN_SCALE = 0.21;

/**
 * @class eg.view360.SpinViewer
 * @classdesc A module used to displays each image sequentially according to the direction of the user's touch movement (left / right) of the sprite image that is collected by rotating the object.
 * @ko            ( / )       .
 * @extends eg.Component
 *
 * @param {HTMLElement} element The element to show the image <ko>   </ko>
 * @param {Object} options The option object<ko> </ko>
 * @param {String} options.imageUrl The url of the sprite image <ko>  url</ko>
 * @param {Number} [options.rowCount=1] Number of horizontal frames in the sprite image <ko>    </ko>
 * @param {Number} [options.colCount=1] Number of vertical frames in the sprite image <ko>    </ko>
 * @param {Number|String} [options.width="auto"] The width of the target element to show the image <ko>    </ko>
 * @param {Number|String} [options.height="auto"] The height of the target element to show the image <ko>    </ko>
 * @param {Boolean} [options.autoHeight=true] Whether to automatically set the height of the image area to match the original image's proportion <ko>         </ko>
 * @param {Number[]} [options.colRow=[0, 0]] The column, row coordinates of the first frame of the sprite image (based on 0 index) <ko>       (column, row)  (0 index )</ko>
 * @param {Number} [options.scale=1] Spin scale (The larger the spin, the more).<ko>Spin  (    )</ko>
 * @support {"ie": "9+", "ch" : "latest", "ff" : "latest",  "sf" : "latest", "edge" : "latest", "ios" : "7+", "an" : "2.3+ (except 3.x)"}
 * @example
 *
 * // Initialize SpinViewer
 * var el = document.getElementById("product-360");
 * var viewer = new eg.view360.SpinViewer(el, {
 * 	imageUrl: "/img/bag360.jpg", // required
 * 	rowCount: 24 //required
 * });
 */
export default class SpinViewer extends Component {
	/**
	 * Version info string
	 * @ko  
	 * @name VERSION
	 * @static
	 * @type {String}
	 * @example
	 * eg.view360.SpinViewer.VERSION;  // ex) 3.0.1
	 * @memberof eg.view360.SpinViewer
	 */
	static VERSION = VERSION;
	constructor(element, options) {
		super();

		this._el = element;

		const opt = Object.assign({}, options);
		const colCount = opt.colCount || 1;
		const rowCount = opt.rowCount || 1;

		this._scale = (opt.scale || 1);
		this._panScale = this._scale * DEFAULT_PAN_SCALE;

		this._frameCount = colCount * rowCount;

		// Init SpriteImage
		this._sprites = new SpriteImage(element, opt).on({
			"load": evt => {
				/**
				 * Events that occur when component loading is complete
				 * @ko     
				 * @name eg.view360.SpinViewer#load
				 * @event
				 * @param {Object} param The object of data to be sent to an event <ko>   </ko>
				 * @param {HTMLElement} param.target The target element for which to display the image <ko>   </ko>
				 * @param {HTMLElement} param.bgElement Generated background image element <ko> background  </ko>
				 *
				 * @example
				 *
				 * viwer.on({
				 *	"load" : function(evt) {
				 *		this.spinBy(360, {duration: 300});
				 *	}
				 * });
				 */
				this.trigger("load", evt);
			},
			"imageError": evt => {
				/**
				 * An event that occurs when the image index is changed by the user's left / right panning
				 * @ko   Panning       
				 * @name eg.view360.SpinViewer#imageError
				 * @event
				 * @param {Object} param The object of data to be sent to an event <ko>   </ko>
				 * @param {String} param.imageUrl User-specified image URL <ko>   URL</ko>
				 *
				 * @example
				 *
				 * viewer.on({
				 *	"imageError" : function(evt) {
				 *		// Error handling
				 *		console.log(e.imageUrl);
				 *	}
				 * });
				 */
				this.trigger("imageError", {
					imageUrl: evt.imageUrl
				});
			}
		});

		// Init Axes
		this._panInput = new PanInput(this._el, {
			scale: [this._panScale, this._panScale]
		});
		this._axes = new Axes({
			angle: {
				range: [0, 359],
				circular: true
			}
		}).on({
			"change": evt => {
				const curr = Math.floor(evt.pos.angle / (360 / this._frameCount));
				const frameIndex = this._frameCount - curr - 1;

				this._sprites.setFrameIndex(frameIndex);

				/**
				 * An event that occurs when the image index is changed by the user's left / right panning
				 * @ko   Panning       
				 * @name eg.view360.SpinViewer#change
				 * @event
				 * @param {Object} param The object of data to be sent to an event <ko>   </ko>
				 * @param {Number[]} param.colRow Column, row of the frame in the sprite image <ko>    column, row</ko>
				 * @param {Number} param.frameIndex Index value that is sequentially appended in Z direction based on col and row.<ko>col, row   Z    index </ko>
				 * @param {Number} param.angle The angle that is currently internally held at an angle between 0 and 359. (not a real product angle) <ko>0 ~ 359        (   )</ko>
				 *
				 * @example
				 *
				 * viwer.on({
				 *	"change" : function(evt) {
				 *		console.log(event.frameIndex, event.colRow, event.angle);   // event.colRow = [0, 4] event.frameIndex = 4, event = 30
				 *	}
				 * });
				 */
				this.trigger("change", {
					frameIndex,
					colRow: this._sprites.getColRow(),
					angle: evt.pos.angle
				});
			},
			"animationEnd": evt => {
				/**
				 * This event is fired when animation ends.
				 * @ko     
				 * @name eg.view360.SpinViewer#animationEnd
				 * @event
				 * @param {Object} param The object of data to be sent to an event <ko>   </ko>
				 * @param {Boolean} param.isTrusted true if an event was generated by the user action, or false if it was caused by a script or API call<ko>     true,  API    false .</ko>
				 *
				 * @example
				 *
				 * viwer.on({
				 *	"animationEnd" : function(evt) {
				 *		// evt.isTrusted === true or false
				 *	}
				 * });
				 */
				this.trigger("animationEnd", {
					isTrusted: evt.isTrusted
				});
			}
		});

		this._axes.connect("angle", this._panInput);
	}

	/**
	 * Set spin scale
	 * @ko scale     
	 * @method eg.view360.SpinViewer#setScale
	 * @param {Number} scale Rotation multiples at spin, the larger the rotation<ko>Spin   ,     </ko>
	 *
	 * @return {Object} Instance of SpinViewer <ko>SpinViewer </ko>
	 *
	 * @example
	 *
	 * viewer.setScale(2);// It moves twice as much.
	 */
	setScale(scale) {
		if (isNaN(scale) || scale < 0) {
			return this;
		}

		this._scale = scale;
		this._panScale = scale * DEFAULT_PAN_SCALE;
		this._panInput.options.scale = [this._panScale, this._panScale];

		return this;
	}

	/**
	 * Get spin scale
	 * @ko scale  .
	 * @method eg.view360.SpinViewer#getScale
	 *
	 * @return {Number} Rotation multiples at spin, the larger the rotation<ko>Spin   ,     </ko>
	 *
	 * @example
	 *
	 * viewer.getScale();// It returns number
	 */
	getScale() {
		return this._scale;
	}

	/**
	 * It gives the effect of rotating for a certain duration by the specified angle based on the current rotation angle.
	 * @ko      (angle)  (duration)   .
	 * @method eg.view360.SpinViewer#spinBy
	 *
	 * @param {Number} [angle = 0] angle<ko>  </ko>
	 * @param {Object} param The parameter object<ko> </ko>
	 * @param {Number} [param.duration = 0] duration<ko>  -  </ko>
	 *
	 * @return {Object} Instance of SpinViewer <ko>SpinViewer </ko>
	 *
	 * @example
	 *
	 * viewer.spinBy(720, {duration: 500});
	 */
	spinBy(angle = 0, param = {duration: 0}) {
		this._axes.setBy({angle}, param.duration);
		return this;
	}

	/**
	 * It gives the effect of rotating for a certain duration (duration) by the specified angle (angle).
	 * @ko  (angle)  (duration)   .
	 * @method eg.view360.SpinViewer#spinTo
	 *
	 * @param {Number} [angle = 0] angle<ko> </ko>
	 * @param {Object} param The parameter object<ko> </ko>
	 * @param {Number} [param.duration = 0] duration<ko>  -  </ko>
	 *
	 * @return {Object} Instance of SpinViewer <ko>SpinViewer </ko>
	 *
	 * @example
	 *
	 * viewer.spinTo(30, {duration:100});
	 */
	spinTo(angle = 0, param = {duration: 0}) {
		this._axes.setTo({angle}, param.duration);
		return this;
	}

	/**
	 * Returns current angles
	 * @ko   .
	 *
	 * @return {Number} Current angle <ko> </ko>
	 */
	getAngle() {
		return this._axes.get().angle || 0;
	}
}
